.main-slider {
  padding-top: 142px;
  padding-bottom: 120px;
  background: url("../img/background-elipse.png") 86% 15% no-repeat;
}

.main-slider__wrapper {
  .container-grid();
  align-items: stretch;
  position: relative;
  z-index: 1;
}

.main-slider__presenter {
  font-size: 13px;
  line-height: 19px;
  text-transform: uppercase;
  padding-left: 10px;
  margin-right: 80px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main-slider__swiper {
  width: 100%;
  max-width: 1000px;
  padding-bottom: 40px;
}

.main-slider__slider-item {
  width: 310px;

  .slider-item__title {
    font-size: 34px;
    font-weight: 400;
    line-height: 34px;
    text-transform: uppercase;
    max-width: 230px;
    margin-bottom: -4px;
    position: relative;
    z-index: 2;
  }

  p {
    font-size: 18px;
    font-weight: 400;
    line-height: 32px;
    max-width: 260px;
    margin-bottom: 20px;
  }

  a {
    font-family: @circe;
    font-size: 34px;
    line-height: 41px;
    cursor: pointer;

    &:hover {
      color: @blue;

      svg {
        fill: @blue;
      }
    }

    span {
      font-family: @roboto;
      font-size: 22px;
      font-style: italic;
      font-weight: 500;
      line-height: 27px;
    }

    svg {
      margin-left: 10px;
      fill: @white;
      transition: all ease-in 0.3s;
    }
  }

  &:nth-child(2) {
    p {
      max-width: 310px;
    }
  }
}

.main-slider__btns {
  width: 120px;
  display: flex;
  justify-content: space-between;
  margin-bottom: -17px;

  .main-slider__btn-prev {
    .btn-reset();
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: @dark-gray;
    padding-left: 6px;

    &:hover {
      background-color: @blue;
      opacity: 1;
    }
  }

  .main-slider__btn-next {
    .btn-reset();
    width: 45px;
    height: 45px;
    border-radius: 50%;
    background-color: @dark-gray;
    padding-right: 6px;

    &:hover {
      background-color: @blue;
      opacity: 1;
    }
  }
}

.swiper-container-horizontal>.swiper-pagination-progressbar,
.swiper-container-vertical>.swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
  top: auto;
  bottom: 0;
  height: 2px;
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background: @white;
}

.swiper-pagination-progressbar {
  background: #343549;
}
