.projects {
  padding-top: 85px;
  padding-bottom: 140px;
}

.projects__wrapper-inner:nth-child(1) {
  .container-grid();
  justify-content: flex-start;
  padding-bottom: 80px;
}

.projects__presenter {
  margin-right: 137px;
  font-size: 13px;
  line-height: 19px;
  text-transform: uppercase;
  padding-left: 8px;
  margin-top: 10px;
}

.projects__content {
  .projects__title {
    font-size: 90px;
    font-weight: 400;
    line-height: 93px;
    text-transform: uppercase;
  }
}

.projects__wrapper-inner:nth-child(2) {
  padding-left: 10px;

  .projects__switch {
    display: flex;
    border-bottom: 1px solid #C3D2D8;
    padding-bottom: 40px;
    margin-bottom: 30px;
  }

  .projects__btn {
    .btn-reset();
    font-family: @osw;
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    margin-right: 60px;

    &:last-child {
      margin-right: 0;
    }

    &--active {
      color: @blue;
      position: relative;

      &::after {
        content: "";
        display: block;
        width: 100%;
        height: 3px;
        background-color: @blue;
        position: absolute;
        left: 0;
        bottom: -42px;
      }
    }
  }
}

.projects__table {
  margin-bottom: 60px;

  tr {
    display: flex;
    border-bottom: 1px solid #C3D2D8;
    padding: 30px 0;

    &:first-child {
      padding-top: 10px;
      padding-bottom: 10px;
    }

    a {
      color: @dark;

      &:hover {
        color: @blue;
      }
    }

    th {
      font-family: @circe;
      font-size: 15px;
      font-style: normal;
      font-weight: 400;
      line-height: 27px;
      color: #9191A1;
      text-align: left;

      &:first-child {
        width: 354px;
        margin-right: 60px;
      }

      &:nth-child(2) {
        width: 373px;
        margin-right: 40px;
      }

      &:nth-child(3) {
        width: 172px;
        margin-right: 24px;
      }

      &:nth-child(4) {
        width: 102px;
        margin-right: auto;
      }

      &:last-child {
        width: 43px;
        text-align: left;
      }
    }

    td {
      color: @dark;

      .projects__table-mobi-txt {
        display: none;
      }

      &:first-child {
        width: 354px;
        margin-right: 60px;

        font-family: @osw;
        font-size: 21px;
        font-weight: 400;
        line-height: 31px;
      }

      &:nth-child(2) {
        width: 373px;
        margin-right: 40px;
        font-family: @circe;
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;
      }

      &:nth-child(3) {
        width: 172px;
        margin-right: 24px;
        font-family: @circe;
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;

        a {
          display: block;
        }
      }

      &:nth-child(4) {
        width: 102px;
        margin-right: 44px;
        font-family: @circe;
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;
      }

      &:last-child {
        width: 43px;
        flex-grow: 1;
        text-align: right;
        font-family: @circe;
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;
      }
    }
  }
}

.pojects__btn-load-more {
  display: block;
  max-width: 374px;
  width: 100%;
  color: @blue;
  border: 1px solid @blue;
  margin-left: auto;
  margin-right: auto;
}
