.footer {
  padding-top: 40px;
  padding-bottom: 40px;
  background: @footer-bg;
}

.footer__wrapper {
  display: flex;
  align-items: center;
  padding-left: 30px;
}

.footer__soc-list {
  display: flex;
  justify-content: space-between;
  width: 76px;
  margin-right: 121px;

  li {
    a {
      svg {
        transition: all ease-in 0.3s;
        fill: @white;
      }

      &:hover {
        svg {
          fill: @blue;
        }
      }
    }
  }
}

.footer__privacy-policy {
  max-width: 386px;
  width: 100%;
  margin-right: 157px;

  a {
    display: block;
    font-size: 15px;
    font-weight: 400;
    line-height: 25px;
    letter-spacing: -0.03em;
    color: @white;

    &:first-child {
      margin-bottom: 14px;
    }

    &:last-child {
      color: rgba(191, 191, 209, 1);
    }
  }
}

.footer__rights {
  margin-top: 40px;
  display: block;
  font-size: 13px;
  font-weight: 400;
  line-height: 25px;
  letter-spacing: -0.03em;
  color: rgba(150, 150, 178, 1);
}
