.about-company {
  padding-top: 85px;
  padding-bottom: 140px;
}

.about-company__top {
  .container-grid();
  position: relative;
  z-index: 2;

  .about-company__presenter {
    font-size: 13px;
    line-height: 19px;
    text-transform: uppercase;
    margin-top: 10px;
    padding-left: 10px;
  }

  .about-company__content {
    display: flex;
    align-items: flex-end;
    margin-right: 16%;
  }

  .about-company__title {
    font-size: 90px;
    line-height: 93px;
    max-width: 823px;
    padding-right: 170px;
    text-transform: uppercase;
    margin-bottom: 0;

    span {
      display: block;

      &:nth-child(2) {
        text-align: right;
      }
    }
  }
}

.about-company__middle {
  margin-top: -30px;
  position: relative;
  z-index: 1;
  margin-bottom: 100px;
}

.about-company__txt {
  display: grid;
  grid-template-columns: 795px 300px;
  grid-template-rows: 354px 512px;
  grid-row-gap: 80px;
  grid-column-gap: 114px;
  padding-top: 24px;

  h3 {
    font-size: 34px;
    line-height: 34px;
    margin-bottom: 36px;
  }

  .about-company__dscr {
    &:first-child {
      grid-row-start: 1;
      grid-row-end: 2;
      grid-column-start: 1;
      grid-column-end: 2;

      div {
        columns: 2;
      }
    }

    &:nth-child(2) {
      margin-left: auto;
      grid-row-start: 1;
      grid-row-end: 3;
      grid-column-start: 2;
      grid-column-end: 3;

      p {
        margin-bottom: 74px;
      }
    }

    &:last-child {
      grid-row-start: 2;
      grid-row-end: 3;
      grid-column-start: 1;
      grid-column-end: 2;
      background-color: #EEEEF2;
      padding-top: 40px;
      padding-bottom: 40px;
      padding-left: 37px;
      padding-right: 12px;
    }
  }
}

.about-company__content-middle {
  display: flex;
  margin-bottom: 55px;

  ul {
    display: flex;
    margin-bottom: 65px;

    li {
      h3 {
        font-size: 23px;
        line-height: 1;
        margin-bottom: 28px;
        max-width: 230px;
      }

      p {
        border: 1px solid @dark;
        border-radius: 20px;
        max-width: 90px;
        text-align: center;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 1;
        letter-spacing: -0.035em;
        padding: 5px 0;
      }

      &:first-child {
        margin-right: 45px;

        h3 {
          max-width: 170px;
        }
      }

      &:last-child {
        margin-right: -30px;
      }
    }
  }
}

.about-company__content-bottom {
  display: flex;

  ol {
    display: flex;
    list-style-type: none;
    /* Убираем исходные маркеры */
    counter-reset: num;
    /* Задаём имя счетчика */
    padding-top: 5px;

    li {
      position: relative;
      padding-top: 44px;

      h3 {
        font-size: 23px;
        line-height: 28px;
        margin-bottom: 8px;
        max-width: 230px;
      }

      p {
        border-radius: 20px;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 32px;
        letter-spacing: -0.035em;
        padding: 5px 0;
      }

      &::before {
        content: "0"counter(num);
        /* Выводим число */
        counter-increment: num;
        /* Увеличиваем значение счётчика */
        font-family: "Arimo", sans-serif;
        display: block;
        color: @dark;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 27px;
        letter-spacing: -0.035em;
        text-align: center;
        width: 27px;
        height: 27px;
        border: 1px solid @dark;
        border-radius: 50%;
        box-sizing: border-box;
        position: absolute;
        top: 0;
        left: 0;
      }

      &:first-child {
        max-width: 98px;
        width: 100%;
        margin-right: 95px;
      }

      &:last-child {
        max-width: 131px;
        width: 100%;
      }
    }
  }
}

.about-company__item-flag {
  width: 370px;
  padding-left: 98px;
  padding-right: 18px;
  position: relative;
  font-size: 20px;

  span {
    display: block;
    width: 80px;
    height: 122px;
    position: absolute;
    top: 0;
    left: 0;
  }

  &:first-child {
    span {
      background-image: url("../img/300-light.png");
      background-size: cover;
      background-position: 50%;
    }
  }

  &:last-child {
    span {
      background-image: url("../img/best-lawyers-light.png");
      background-size: cover;
      background-position: 50%;
    }
  }
}
