.main-contacts {
  padding-top: 70px;
  padding-bottom: 115px;

  .btn {
    max-width: 448px;
    width: 100%;

  }
}

.main-contacts__wrapper {
  .container-grid();
}

.main-contacts__presenter {
  font-size: 13px;
  line-height: 19px;
  text-transform: uppercase;
  padding-left: 10px;
}

.main-contacts__content {
  width: 100%;
  max-width: 1230px;
  padding-left: 120px;
  display: flex;
  background:
    url("../img/background-elipse.png") 0% 0% / contain no-repeat,
    url("../img/map-shadow.png") 100% 0 no-repeat,
    url("../img/map.png") 100% 0 no-repeat;
}

.main-contacts__txt {
  position: relative;
  z-index: 2;
}

.main-contacts__title {
  font-size: 34px;
  font-weight: 400;
  line-height: 34px;
  text-transform: uppercase;
  max-width: 310px;
  margin-bottom: 58px;
}

.main-contacts__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 456px;


  li {
    max-width: 206px;
    margin-bottom: 38px;

    span {
      font-size: 15px;
      font-style: normal;
      font-weight: 400;
      line-height: 27px;
      display: block;
      margin-bottom: 12px;
    }

    div,
    div a {
      font-family: @circe;
      font-size: 21px;
      font-style: normal;
      font-weight: 400;
      line-height: 38px;
      color: @white;
    }

    &:last-child {
      position: relative;

      .main-contacts__download-title {
        font-family: @osw;
        font-size: 44px;
        font-weight: 400;
        line-height: 40px;
        color: @white;
        text-transform: uppercase;
        white-space: nowrap;
      }

      .main-contacts__arrow-btn {
        position: absolute;
        top: -10px;
        right: -85px;
        border: 1px solid @white;
        border-radius: 50%;
        width: 75px;
        height: 75px;
        display: flex;
        align-items: center;
        justify-content: center;

        svg {
          fill: @white;
        }
      }

      .main-contacts__download {
        font-size: 18px;
        font-weight: 400;
        line-height: 26px;
      }
    }
  }
}

.main-contacts__map {
  margin-left: -148px;
  margin-top: -60px;
  position: relative;
  z-index: 1;
  width: 751px;
  height: 714px;

  .main-contacts__pin {
    position: absolute;
    top: 307px;
    left: 431px;
  }
}
