@font-face {
  font-family: "Circe", sans-serif;
  src: url("../fonts/Circe-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 21px;
  line-height: 32px;
  font-family: "Circe", sans-serif;
}
main {
  min-height: 78vh;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
ol {
  padding: 0;
  margin: 0;
}
p {
  margin: 0;
}
.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}
a {
  text-decoration: none;
  transition: all ease-in 0.3s;
  outline: none;
  font-family: inherit;
}
button {
  outline: none;
  cursor: pointer;
  transition: all ease-in 0.2s;
}
button:hover {
  opacity: 0.8;
}
button:active,
button:focus {
  opacity: 0.6;
}
.container {
  max-width: 1240px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;
}
.overlay {
  width: 100%;
  height: 100%;
  z-index: 8;
  background-color: rgba(255, 255, 255, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  backdrop-filter: blur(8px);
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  margin-top: 0;
}
.dark {
  background-color: #0B0B26;
  color: #ffffff;
}
.light {
  background-color: #ffffff;
  color: #0B0B26;
}
.light .header__logo svg {
  fill: #0B0B26;
}
.light .header__serch-btn svg circle {
  stroke: #0B0B26;
}
.light .header__serch-btn svg rect {
  fill: #0B0B26;
}
.light .header__languages-btn {
  color: #0B0B26;
  border-color: #0B0B26;
}
.header {
  min-height: 140px;
  padding-top: 28px;
}
.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 27px;
  padding-right: 13px;
}
.header__logo {
  margin-right: 63px;
}
.header__logo svg {
  fill: #ffffff;
}
.header__nav {
  margin-top: 28px;
}
.header__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__list li {
  margin-right: 68px;
}
.header__list li.active a {
  color: #29A9C8;
}
.header__list li:last-child {
  margin-right: 0;
}
.header__list li a {
  font-size: 18px;
}
.header__user {
  margin-left: auto;
  display: flex;
  align-items: center;
  margin-top: 33px;
}
.header__serch-btn {
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  height: 33px;
  margin-right: 23px;
}
.header__serch-btn svg circle {
  stroke: #ffffff;
}
.header__serch-btn svg rect {
  fill: #ffffff;
}
.header__languages-btn {
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  font-family: "Circe", sans-serif;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  width: 30px;
  height: 30px;
  border: 1px solid #ffffff;
  color: #ffffff;
  border-radius: 50%;
}
.footer {
  padding-top: 40px;
  padding-bottom: 40px;
  background: #03031F;
}
.footer__wrapper {
  display: flex;
  align-items: center;
  padding-left: 30px;
}
.footer__soc-list {
  display: flex;
  justify-content: space-between;
  width: 76px;
  margin-right: 121px;
}
.footer__soc-list li a svg {
  transition: all ease-in 0.3s;
  fill: #ffffff;
}
.footer__soc-list li a:hover svg {
  fill: #29A9C8;
}
.footer__privacy-policy {
  max-width: 386px;
  width: 100%;
  margin-right: 157px;
}
.footer__privacy-policy a {
  display: block;
  font-size: 15px;
  font-weight: 400;
  line-height: 25px;
  letter-spacing: -0.03em;
  color: #ffffff;
}
.footer__privacy-policy a:first-child {
  margin-bottom: 14px;
}
.footer__privacy-policy a:last-child {
  color: #bfbfd1;
}
.footer__rights {
  margin-top: 40px;
  display: block;
  font-size: 13px;
  font-weight: 400;
  line-height: 25px;
  letter-spacing: -0.03em;
  color: #9696b2;
}
.btn {
  padding: 0;
  background-color: transparent;
  border: none;
  outline: none;
  font-family: "Circe", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 43px;
  letter-spacing: 0.02em;
  color: #ffffff;
  padding-top: 12px;
  padding-bottom: 11px;
}
.btn--blue {
  background-color: #29A9C8;
}
