.main-info {
  padding-top: 20px;
  padding-bottom: 67px;
  min-height: 1314px;
  background: url("../img/main-bg.png") 104% 100% no-repeat, url("../img/background-elipse.png") 6% 62% no-repeat;
}
.main-info__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.main-info__presenter {
  font-size: 13px;
  line-height: 19px;
  text-transform: uppercase;
  padding-left: 8px;
}
.main-info__content {
  flex-grow: 1;
}
.main-info__ol {
  display: flex;
  flex-direction: row-reverse;
  min-height: 510px;
  list-style-type: none;
  /* Убираем исходные маркеры */
  counter-reset: num;
  /* Задаём имя счетчика */
}
.main-info__ol .main-info__ol-item {
  width: 100%;
  max-width: 444px;
  min-height: 270px;
  margin-top: 55px;
  position: relative;
}
.main-info__ol .main-info__ol-item:hover .main-info__ol-item-wrapper {
  background: linear-gradient(180deg, rgba(41, 169, 200, 0) 0%, #29A9C8 100%);
}
.main-info__ol .main-info__ol-item:hover a svg {
  opacity: 1;
  fill: #ffffff;
}
.main-info__ol .main-info__ol-item .main-info__ol-item-wrapper {
  min-height: 270px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px 40px;
  transition: all ease-in 1s;
}
.main-info__ol .main-info__ol-item h3 {
  font-size: 34px;
  line-height: 30px;
  text-transform: uppercase;
  transform: translate(-40px, -118%);
  max-width: 275px;
}
.main-info__ol .main-info__ol-item a {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  font-size: 18px;
  line-height: 43px;
  color: #ffffff;
}
.main-info__ol .main-info__ol-item a svg {
  transition: all ease-in 0.1s;
  opacity: 0;
}
.main-info__ol .main-info__ol-item::before {
  content: "0" counter(num);
  /* Выводим число */
  counter-increment: num;
  /* Увеличиваем значение счётчика */
  color: #ffffff;
  font-family: "Oswald", sans-serif;
  font-size: 40px;
  font-style: normal;
  font-weight: 300;
  line-height: 36px;
  letter-spacing: -0.035em;
  text-align: left;
  position: absolute;
  top: 26px;
  right: 24px;
}
.main-info__ol .main-info__ol-item:first-child {
  margin-bottom: auto;
  margin-left: 50px;
  margin-right: -3px;
  background: url("../img/ol-1-bg-1.png") 0 0 no-repeat, url("../img/ol-1-bg-2.png") 0 0 no-repeat;
}
.main-info__ol .main-info__ol-item:last-child {
  margin-top: auto;
  max-width: 518px;
  margin-left: -2px;
  background: url("../img/ol-2-bg-1.png") 5px 5px no-repeat, url("../img/ol-2-bg-2.png") 5px 5px no-repeat;
}
