.main-info__wrapper--more-info .main-info__content .main-info__content-bottom {
  display: flex;
}
.main-info__wrapper--more-info .main-info__content .main-info__content-bottom ol {
  display: flex;
  list-style-type: none;
  /* Убираем исходные маркеры */
  counter-reset: num;
  /* Задаём имя счетчика */
}
.main-info__wrapper--more-info .main-info__content .main-info__content-bottom ol li {
  position: relative;
  padding-top: 44px;
}
.main-info__wrapper--more-info .main-info__content .main-info__content-bottom ol li h3 {
