  max-width: 230px;
}
.main-info__wrapper--more-info .main-info__content .main-info__content-bottom ol li p {
  border-radius: 20px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.035em;
  padding: 5px 0;
}
.main-info__wrapper--more-info .main-info__content .main-info__content-bottom ol li::before {
  content: "0" counter(num);
  /* Выводим число */
