.tax-issues {
  padding-top: 85px;
  background:
    url("../img/waves.png") 0 50px no-repeat,
    url("../img/waves.png") 0 107% no-repeat;
  background-size: 100% 745px;
}

.tax-issues__wrapper-top {
  display: flex;
  flex-direction: column;
  padding-bottom: 125px;


  .tax-issues__wrapper-inner:nth-child(1) {
    .container-grid();
    margin-bottom: 113px;

    .tax-issues__presenter {
      margin-right: 132px;
      font-size: 13px;
      line-height: 19px;
      text-transform: uppercase;
      padding-left: 8px;
      margin-top: 10px;
    }

    .tax-issues__title {
      font-size: 90px;
      font-weight: 400;
      line-height: 93px;
      text-transform: uppercase;
    }

    .tax-issues__slogan {
      font-size: 21px;
      font-weight: 400;
      line-height: 31px;
      padding-right: 240px;
    }
  }

  .tax-issues__wrapper-inner:nth-child(2) {
    display: flex;
    flex-wrap: wrap;
    max-width: 828px;
    margin-left: auto;
    margin-right: auto;

    .tax-issues__list-wrapper {
      margin-right: 112px;

      &:first-child {
        margin-bottom: 86px;
      }

      &:nth-child(2) {
        ul {
          li {
            &:nth-child(2) {
              margin-bottom: 40px;
            }

            &:nth-child(3) {
              margin-bottom: 60px;
              padding-top: 20px;

              span {
                top: 20px;
              }
            }
          }
        }
      }

      h3 {
        font-size: 34px;
        font-weight: 400;
        line-height: 31px;
        margin-bottom: 66px;
        text-transform: uppercase;
      }

      ul {
        max-width: 346px;

        li {
          font-family: @osw;
          font-size: 25px;
          font-weight: 300;
          line-height: 30px;
          padding-left: 105px;
          position: relative;
          margin-bottom: 41px;

          span {
            position: absolute;
            top: 10px;
            left: 5px;
            display: block;
            width: 72px;
            height: 72px;
            background: url("../img/bg-icon.png") 50%;
            background-size: cover;
            object-fit: contain;

            svg {
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
            }
          }
        }
      }
    }

    .tax-issues__spec-project {
      position: sticky;
      top: 20px;
      width: 365px;
      height: 365px;
      border-radius: 50%;
      text-align: center;
      padding-top: 90px;
      background:
        url("../img/spec-1.png") 160px 20px no-repeat,
        url("../img/spec-2.png") 48px 205px no-repeat,
        url("../img/spec-3.png") 272px 224px no-repeat,
        url("../img/spec-main-bg.png") 50% 50% no-repeat,
        url("../img/spec-gradient.png") 50%/cover no-repeat;

      a {
        font-size: 18px;
        font-weight: 400;
        line-height: 26px;
        color: @white;
        display: block;
        margin-bottom: 7px;
      }

      h3 {
        padding: 15px;

        a {
          font-size: 41px;
          font-weight: 400;
          line-height: 45px;
          text-transform: uppercase;
        }
      }
    }
  }
}

.tax-issues__wrapper-middle {
  display: flex;
  flex-direction: column;

  .tax-issues__wrapper-inner:nth-child(1) {
    .container-grid();

    .tax-issues__presenter {
      max-width: 112px;
      margin-right: 74px;
      font-size: 13px;
      line-height: 19px;
      text-transform: uppercase;
      padding-left: 8px;
    }

    .tax-issues__title {
      font-size: 34px;
      font-weight: 400;
      line-height: 34px;
      margin-bottom: 38px;
      text-transform: uppercase;
      padding-right: 170px;
    }

    .tax-issues__content {
      padding-right: 140px;
    }
  }

  .tax-issues__wrapper-inner:nth-child(2) {
    height: 1030px;
    padding-top: 181px;
    padding-bottom: 115px;
    padding-left: 20px;
    margin-left: auto;
    margin-right: auto;

    .tax-issues__circle {
      width: 674px;
      height: 674px;
      position: relative;

      border-radius: 50%;
      border: 1px solid @blue;

      .tax-issues__item {
        font-size: 18px;
        font-weight: 400;
        line-height: 27px;
        position: absolute;
        width: 230px;

        padding-left: 20px;
        padding-right: 20px;
        text-align: center;
        background-color: @white;

        .line {
          display: block;
          width: 60px;
          height: 1px;
          background-color: @dark;
          position: absolute;
        }

        span {
          position: absolute;
          display: block;
          width: 72px;
          height: 72px;
          background: url("../img/bg-icon.png") 50%;
          background-size: cover;
          object-fit: contain;

          svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }

        &:first-child {
          left: 50%;
          top: -6%;
          transform: translateX(-50%);

          span {
            bottom: -117%;
            left: 35%;
          }

          .line {
            transform: rotate(90deg);
            bottom: -174%;
            left: 37.5%;
          }
        }

        &:nth-child(2) {
          top: 20.5%;
          right: -16%;

          span {
            top: 37%;
            left: -18%;
          }

          .line {
            transform: rotate(150deg);
            bottom: 11%;
            left: -45.5%;
          }
        }

        &:nth-child(3) {
          top: 61.5%;
          right: -16%;

          span {
            top: 1%;
            left: -18%;
          }

          .line {
            transform: rotate(-150deg);
            top: 0%;
            left: -45.5%;
          }
        }

        &:nth-child(4) {
          left: 50%;
          width: 360px;
          bottom: -5%;
          z-index: 1;
          transform: translateX(-50%);

          span {
            top: -110%;
            left: 40%;
          }

          .line {
            transform: rotate(90deg);
            top: -164%;
            left: 42%;
          }
        }

        &:nth-child(5) {
          top: 62%;
          left: -20%;

          span {
            top: 0;
            right: -31%;
          }

          .line {
            transform: rotate(150deg);
            top: -4%;
            right: -60%;
          }
        }

        &:nth-child(6) {
          top: 16.5%;
          left: -14%;
          padding: 0 23px;

          span {
            top: 72%;
            right: -13%;
          }

          .line {
            transform: rotate(-150deg);
            bottom: -37%;
            right: -42%;
          }
        }
      }

      .tax-issues__main {
        width: 160px;
        height: 160px;
        box-sizing: content-box;
        padding: 30px;
        border: 1px solid @dark;
        border-radius: 50%;
        background-image: url("../img/main-circle.png");
        background-size: 164px 164px;
        background-repeat: no-repeat;
        background-position: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }

  .tax-issues__wrapper-inner:nth-child(3) {
    margin-left: auto;
    margin-right: auto;

    h3 {
      font-size: 34px;
      font-weight: 400;
      line-height: 31px;
      text-transform: uppercase;
      margin-bottom: 80px;
    }

    ul {
      display: flex;
      flex-wrap: wrap;
      max-width: 828px;

      li {
        padding-left: 100px;
        max-width: 346px;
        font-family: @osw;
        font-size: 25px;
        font-weight: 300;
        line-height: 30px;
        position: relative;
        margin-bottom: 25px;

        &:nth-child(2n) {
          margin-left: 77px;
        }

        &:nth-child(3),
        &:nth-child(4) {
          span {
            top: 20px;
          }
        }

        span {
          position: absolute;
          top: 0;
          left: 0;
          display: block;
          width: 72px;
          height: 72px;
          background: url("../img/bg-icon.png") 50%;
          background-size: cover;
          object-fit: contain;

          svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  }
}


.tax-issues__wrapper-bottom {
  padding-top: 100px;

  // slider
  .tax-issues__wrapper-inner:nth-child(1) {
    .tax-issues-slider__content {
      max-width: 828px;
      flex-grow: 1;
    }

    .tax-issues-slider__wrapper {
      .container-grid();
      justify-content: flex-start;
      align-items: stretch;
      position: relative;
      z-index: 1;
    }

    .tax-issues-slider__presenter {
      font-size: 13px;
      line-height: 19px;
      text-transform: uppercase;
      padding-left: 10px;
      margin-right: 65px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-top: 5px;
    }

    .tax-issues-slider__swiper {
      width: 100%;
      max-width: 1000px;
      padding-bottom: 93px;
    }

    .tax-issues-slider__swiper-wraper {
      list-style-type: none;
      /* Убираем исходные маркеры */
      counter-reset: num;
      /* Задаём имя счетчика */
    }

    .tax-issues-slider__slider-item {
      width: 345px;
      padding-top: 50px;
      padding-right: 40px;


      font-size: 18px;
      font-weight: 400;
      line-height: 25px;

      &::before {
        content: "0"counter(num);
        /* Выводим число */
        counter-increment: num;
        font-family: "Arimo", sans-serif;
        font-size: 16px;
        font-weight: 400;
        font-style: normal;
        line-height: 27px;
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        box-sizing: border-box;
        width: 27px;
        height: 27px;
        text-align: center;
        letter-spacing: -.035em;
        color: @dark;
        border: 1px solid @dark;
        border-radius: 50%;
      }
    }

    .tax-issues-slider__btns {
      width: 120px;
      display: flex;
      justify-content: space-between;
      margin-bottom: -17px;

      .tax-issues-slider__btn-prev {
        .btn-reset();
        width: 45px;
        height: 45px;
        border-radius: 50%;
        background-color: @dark-gray;
        padding-left: 6px;

        &:hover {
          background-color: @blue;
          opacity: 1;
        }
      }

      .tax-issues-slider__btn-next {
        .btn-reset();
        width: 45px;
        height: 45px;
        border-radius: 50%;
        background-color: @dark-gray;
        padding-right: 6px;

        &:hover {
          background-color: @blue;
          opacity: 1;
        }
      }
    }

    .swiper-container-horizontal>.swiper-pagination-progressbar,
    .swiper-container-vertical>.swiper-pagination-progressbar.swiper-pagination-progressbar-opposite {
      top: auto;
      bottom: 0;
      height: 2px;
    }

    .swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
      background: @blue;
    }

    .swiper-pagination-progressbar {
      background: #CEDCE1;
    }
  }

  .tax-issues__wrapper-inner:nth-child(2) {
    .container-grid();
    justify-content: flex-start;

    padding-top: 185px;
    padding-bottom: 237px;

    .tax-issues__presenter {
      font-size: 13px;
      line-height: 19px;
      text-transform: uppercase;
      padding-left: 10px;
      margin-right: 100px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-top: 25px;
    }

    .tax-issues__content {
      display: flex;
      align-items: center;
      width: 100%;

      .tax-issues__link {
        font-family: @osw;
        font-size: 44px;
        font-weight: 400;
        line-height: 40px;
        text-transform: uppercase;
        color: @dark;
      }
    }

    .tax-issues__arrow-btn {
      width: 102px;
      height: 102px;
      border-radius: 50%;
      background-color: #19233F;
      display: flex;
      justify-content: center;
      align-items: center;
      margin-right: 42px;

      svg {
        fill: @white;
        transform: scale(1.4);
      }
    }

    .tax-issues__up {
      border-radius: 50%;
      background-color: transparent;
      border: 1px solid @blue;
      width: 60px;
      height: 60px;
      font-family: @osw;
      font-size: 15px;
      line-height: 15px;
      color: @blue;
      margin-left: auto;
    }
  }
}
