.header {
  min-height: 140px;
  padding-top: 28px;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 27px;
  padding-right: 13px;
}

.header__logo {
  margin-right: 63px;

  svg {
    fill: @white;
  }
}

.header__nav {
  margin-top: 28px;
}

.header__list {
  display: flex;
  justify-content: space-between;
  align-items: center;

  li {
    &.active {
      a {
        color: @blue;
      }
    }

    margin-right: 68px;

    &:last-child {
      margin-right: 0;
    }

    a {
      font-size: 18px;
    }
  }
}

.header__user {
  margin-left: auto;
  display: flex;
  align-items: center;
  margin-top: 33px;
}

.header__serch-btn {
  .btn-reset();
  height: 33px;
  margin-right: 23px;


  svg {
    circle {
      stroke: @white;
    }

    rect {
      fill: @white;
    }
  }
}

.header__languages-btn {
  .btn-reset();
  font-family: @circe;
  font-size: 13px;
  font-weight: 400;
  line-height: 1;
  width: 30px;
  height: 30px;
  border: 1px solid @white;
  color: @white;
  border-radius: 50%;
}
