.services {
  .main-start-screen {
    padding-top: 85px;
    background: none;
    padding-bottom: 110px;

    .main-start-screen__presenter {
      margin-top: 15px;
    }

    .main-start-screen__title {
      padding-right: 56px;
    }
  }

  .main-info {
    background: url("../img/main-bg-light.png") 103% -21% no-repeat;

    .main-info__presenter {
      margin-top: 8px;
    }
  }

  .main-info__wrapper--more-info .main-info__content .main-info__item-flag--300 span {
    background-image: url("../img/300-red-black.png") !important;
    background-position: 50%;
    background-size: cover;
  }

  .main-info__wrapper--more-info .main-info__content .main-info__item-flag:last-child span {
    background-image: url("../img/best-lawyers-red-black.png");
    background-position: 50%;
    background-size: cover;
  }
}
