.profile {
  padding-top: 15px;
  padding-bottom: 100px;
}

.profile__wrapper {
  .container-grid();
}

.profile__img-wrapper {
  width: 374px;
  height: 500px;
  margin-right: 40px;
  margin-top: 10px;

  img {
    object-fit: cover;
  }
}

.profile__content {
  h1 {
    font-size: 60px;
    font-weight: 400;
    line-height: 60px;
    margin-bottom: 23px;
  }
}

.profile__email-wrapper {
  margin-bottom: 44px;

  p {
    font-size: 18px;
    line-height: 25px;
    color: #6C6C76;
  }

  a {
    font-size: 21px;
    line-height: 38px;
    color: @dark;
  }
}

.profile__specialization {
  margin-bottom: 50px;

  h3 {
    font-size: 34px;
    line-height: 34px;
    margin-bottom: 20px;
  }

  p {
    font-size: 18px;
    line-height: 25px;
    max-width: 200px;
  }
}

.profile__projects {
  h3 {
    font-size: 34px;
    line-height: 34px;
    margin-bottom: 40px;
  }

  ol {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    list-style-type: none;
    /* Убираем исходные маркеры */
    counter-reset: num;
    /* Задаём имя счетчика */
    margin-bottom: 20px;

    li {
      font-size: 18px;
      line-height: 25px;
      max-width: 370px;
      padding-top: 40px;
      position: relative;
      margin-bottom: 36px;

      &::before {
        content: "0"counter(num);
        /* Выводим число */
        counter-increment: num;
        /* Увеличиваем значение счётчика */
        font-family: "Arimo", sans-serif;
        display: block;
        color: @dark;
        font-size: 16px;
        font-style: normal;
        font-weight: 400;
        line-height: 27px;
        letter-spacing: -0.035em;
        text-align: center;
        width: 27px;
        height: 27px;
        border: 1px solid @dark;
        border-radius: 50%;
        box-sizing: border-box;
        position: absolute;
        top: 0;
        left: 0;
      }
    }
  }
}

.profile__dscr {
  h3 {
    font-size: 34px;
    line-height: 34px;
    margin-bottom: 40px;
  }

  .profile__dscr-content {
    font-size: 18px;
    line-height: 25px;

    p {
      margin-bottom: 20px;
    }
  }
}
