.team {
  background-image: url("../img/waves.png");
  background-size: 100% 765px;
  background-position: 0 0;
  background-repeat: no-repeat;
}

.team__wrapper {
  .team__wrapper-top {
    .container-grid();
    padding-top: 95px;
    padding-bottom: 75px;
  }

  .team__wrapper-bottom {
    padding-bottom: 35px;
  }
}

.team__presenter {
  margin-right: 112px;
}

.team__content {
  margin-right: auto;

  .team__title {
    max-width: 848px;
    font-size: 90px;
    font-style: normal;
    font-weight: 400;
    line-height: 93px;
    text-transform: uppercase;
  }
}

.team__list {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.team__item {
  width: 100%;
  max-width: 555px;
  height: 400px;
  margin-bottom: 87px;

  &:hover {
    .team__item-name {
      color: @blue;
    }

    .team__item-txt {

      .team__item-dscr,
      .team__item-link {
        opacity: 1;
      }
    }
  }
}

.team__item-wrapper {
  display: flex;
  justify-content: space-between;
  height: 400px;
}

.team__item-txt {
  display: flex;
  flex-direction: column;
  padding-bottom: 25px;

  .team__item-name {
    max-width: 200px;
    font-size: 34px;
    font-weight: 400;
    line-height: 34px;
    margin-bottom: 20px;
    transition: all ease-in 0.3s;
  }

  .team__item-position {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    max-width: 160px;
    margin-bottom: 27px;
  }

  .team__item-dscr {
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 25px;
    max-width: 215px;
    margin-bottom: auto;
    opacity: 0;
    transition: all ease-in 0.3s;
  }

  .team__item-link {
    font-family: @circe;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 43px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: @dark;
    opacity: 0;

    svg {
      fill: @dark;
    }
  }
}

.team__item-img-wrapper {
  height: 400px;
  width: 300px;
  background-image: url("../img/partner-layout.jpg");
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}
