.main-statistics {
  padding-top: 135px;
  padding-bottom: 125px;
  background-color: @dark-gray;
}

.main-statistics__wrapper {
  .container-grid();
}

.main-statistics__presenter {
  font-size: 13px;
  line-height: 19px;
  text-transform: uppercase;
  padding-left: 10px;
  margin-right: 80px;
}

.main-statistics__content {
  width: 100%;
  display: flex;
}

.main-statistics__item {
  margin-right: 54px;

  &:nth-child(2) {
    margin-right: 104px;
  }

  &:nth-child(3) {
    margin-right: 0;

    .main-statistics__int {
      span {
        margin-left: -12px;
        font-family: @osw;
        font-size: 58px;
        font-style: normal;
        font-weight: 400;
        line-height: 58px;
        text-transform: uppercase;
      }
    }
  }

  .main-statistics__int {
    font-size: 90px;
    font-style: normal;
    font-weight: 400;
    line-height: 90px;
    margin-bottom: 28px;
  }

  .main-statistics__txt {
    font-size: 21px;
    font-weight: 400;
    line-height: 31px;
    max-width: 200px;
  }
}
