.main-start-screen {
  padding-top: 250px;
  padding-bottom: 200px;
  background-image: url("../img/waves.png");
  background-size: cover;
  background-position: 0 0;
  background-repeat: no-repeat;
}

.main-start-screen__wrapper {
  .container-grid();
}

.main-start-screen__presenter {
  font-size: 13px;
  line-height: 19px;
  text-transform: uppercase;
  padding-left: 10px;
}

.main-start-screen__content {
  display: flex;
  align-items: flex-end;
}

.main-start-screen__title {
  font-size: 90px;
  line-height: 93px;
  max-width: 823px;
  padding-right: 200px;
  text-transform: uppercase;
  margin-bottom: 0;

  span {
    display: block;

    &:nth-child(2) {
      text-align: right;
    }
  }
}

.main-start-screen__tel {
  font-size: 21px;
  color: @white;
}
