.main-info {
  padding-top: 20px;
  padding-bottom: 67px;
  min-height: 1314px;
  background:
    url("../img/main-bg.png") 104% 100% no-repeat,
    url("../img/background-elipse.png") 6% 62% no-repeat;
}

.main-info__wrapper {
  .container-grid();
}

.main-info__presenter {
  font-size: 13px;
  line-height: 19px;
  text-transform: uppercase;
  padding-left: 8px;
}

.main-info__content {
  flex-grow: 1;
}

.main-info__ol {
  display: flex;
  flex-direction: row-reverse;
  min-height: 510px;
  list-style-type: none;
  /* Убираем исходные маркеры */
  counter-reset: num;
  /* Задаём имя счетчика */

  .main-info__ol-item {
    width: 100%;
    max-width: 444px;
    min-height: 270px;
    margin-top: 55px;
    position: relative;

    &:hover {
      .main-info__ol-item-wrapper {
        background: @blue-gradient;
      }

      a {
        svg {
          opacity: 1;
          fill: @white;
        }
      }
    }

    .main-info__ol-item-wrapper {
      min-height: 270px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      padding: 20px 40px;
      transition: all ease-in 1s;
    }

    h3 {
      font-size: 34px;
      line-height: 30px;
      text-transform: uppercase;
      transform: translate(-40px, -118%);
      max-width: 275px;
    }

    a {
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      font-size: 18px;
      line-height: 43px;
      color: @white;

      svg {
        transition: all ease-in 0.1s;
        opacity: 0;
      }
    }

    &::before {
      content: "0"counter(num);
      /* Выводим число */
      counter-increment: num;
      /* Увеличиваем значение счётчика */
      color: @white;
      font-family: @osw;
      font-size: 40px;
      font-style: normal;
      font-weight: 300;
      line-height: 36px;
      letter-spacing: -0.035em;
      text-align: left;
      position: absolute;
      top: 26px;
      right: 24px;
    }

    &:first-child {
      margin-bottom: auto;
      margin-left: 50px;
      margin-right: -3px;
      background:
        url("../img/ol-1-bg-1.png") 0 0 no-repeat,
        url("../img/ol-1-bg-2.png") 0 0 no-repeat;
    }

    &:last-child {
      margin-top: auto;
      max-width: 518px;
      margin-left: -2px;
      background:
        url("../img/ol-2-bg-1.png") 5px 5px no-repeat,
        url("../img/ol-2-bg-2.png") 5px 5px no-repeat;
    }
  }
}

.main-info__wrapper--practics {
  margin-bottom: 60px;
}


.main-info__wrapper--more-info {
  .main-info__content {
    max-width: 1000px;
    display: flex;
    flex-wrap: wrap;

    .main-info__content-top {
      margin-bottom: 82px;
      width: 100%;

      span {
        font-size: 20px;
        display: block;
        max-width: 518px;
      }
    }

    .main-info__list-flag {
      max-width: 518px;
      margin-right: 45px;
    }

    .main-info__item-flag {
      padding-left: 132px;
      position: relative;

      span {
        display: block;
        width: 80px;
        height: 122px;
        position: absolute;
        top: 0;
        left: 0;
      }

      &:first-child {
        margin-bottom: 95px;

        span {
          background-image: url("../img/300.png");
          background-size: cover;
          background-position: 50%;
        }
      }

      &:last-child {
        span {
          background-image: url("../img/best-lawyers.png");
          background-size: cover;
          background-position: 50%;
        }
      }
    }

    .main-info__content-middle {
      display: flex;

      ul {
        display: flex;
        margin-bottom: 105px;

        li {
          h3 {
            font-size: 34px;
            line-height: 1;
            margin-bottom: 26px;
            max-width: 230px;
          }

          p {
            border: 1px solid @white;
            border-radius: 20px;
            max-width: 90px;
            text-align: center;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 1;
            letter-spacing: -0.035em;
            padding: 5px 0;
          }

          &:first-child {
            margin-right: 45px;

            h3 {
              max-width: 170px;
            }
          }

          &:last-child {
            margin-right: -30px;
          }
        }
      }
    }

    .main-info__content-bottom {
      display: flex;

      ol {
        display: flex;
        list-style-type: none;
        /* Убираем исходные маркеры */
        counter-reset: num;
        /* Задаём имя счетчика */

        li {
          position: relative;
          padding-top: 44px;

          h3 {
            font-size: 34px;
            line-height: 1;
            margin-bottom: 8px;
            max-width: 230px;
          }

          p {
            border-radius: 20px;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 32px;
            letter-spacing: -0.035em;
            padding: 5px 0;
          }

          &::before {
            content: "0"counter(num);
            /* Выводим число */
            counter-increment: num;
            /* Увеличиваем значение счётчика */
            font-family: "Arimo", sans-serif;
            display: block;
            color: @white;
            font-size: 16px;
            font-style: normal;
            font-weight: 400;
            line-height: 27px;
            letter-spacing: -0.035em;
            text-align: center;
            width: 27px;
            height: 27px;
            border: 1px solid @white;
            border-radius: 50%;
            box-sizing: border-box;
            position: absolute;
            top: 0;
            left: 0;
          }

          &:first-child {
            max-width: 98px;
            width: 100%;
            margin-right: 116px;
          }

          &:last-child {
            max-width: 131px;
            width: 100%;
          }
        }
      }
    }
  }
}
